@import "../../utils/variables.scss";

.wrapper {
  padding-top: calc(#{$navbarHeight});
  background: #f1f1f1;
  .header {
    background: url("../../assets//images/aboutUs/Background.png") center/cover
      no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 1.8rem;
    font-weight: 600;
    min-height: 30vh;
    text-align: center;
    height: 25vh;
    width: 100%;
  }
  .main {
    display: flex;
    justify-content: space-between;
  }
  .text {
    color: #666666;
    display: flex;
    flex-direction: column;
    //   align-items: center;
    justify-content: center;
    text-align: justify;
    //   text-align: center;
    align-items: center;
    margin: 50px;
    font-size: 16px;
    padding-left: 40px;
    padding-bottom: 10px;
    padding-right: 40px;
    font-weight: 300;
    line-height: 28.8px;
  }
  .details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    font-size: 16px;
  }
}

@media screen and (max-width: 956px) {
  .main {
    flex-direction: column;
    .left {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 3em;
    }

    .right {
      padding-left: 0;
      width: 100%;
      min-height: 500px;
    }
  }
}

@media screen and (max-width: 1000px) {
  .header {
    min-height: 30vh; // Adjust the height for smaller screens
  }
}

@media screen and (min-width: 0) and (max-width: 520px) {
  .wrapper {
    .text {
      align-items: center;
      margin: 10px !important;
      font-size: 18px;
      padding: 30px !important;
      font-weight: 300;
      line-height: 28.8px;
    }
  }
}
